.make-trade-header span{
    font-family: 'KoHo', sans-serif;
    font-size:1.25rem;
    position:relative;
    bottom:10px;
    padding:5px 5px 10px;
}

.make-trade-body h3{
    font-family: 'Kodchasan', sans-serif;
    margin-left:10px;
}

.make-trade-btn{
    background:#1eabc4;
}

.make-trade-btn:hover{
    background:#E23F35;
}

.trade-recipient{
    background:#FBF6EF;
    padding:10px;
}

.trade-recipient h4, .trade-recipient-record h4{
    font-family: 'KoHo', sans-serif;
    font-size:1.5rem;
    margin:0 0 20px;
}

.trade-your-record h4{
    font-family: 'KoHo', sans-serif;
    font-size:2rem;
    margin:0 0 20px;
}

.recipient-avatar{
    border-radius:50%;
}

.recipient-avatar, .recipient-rec-cover, .your-rec-cover{
    width:100%;
}

.trade-recipient-record, .trade-your-record{
    background:#E23F35;
    padding:10px;
    /* filter:grayscale(100%); */
}

.trade-recipient-record h4, .trade-your-record h4{
    color:#FBF6EF;
}


.trade-recipient .row, .trade-recipient-record .row, .trade-your-record .row{
    margin:0;
}

.user-select, .record-select{
    display:block;
    margin-bottom:20px;
}

.filled .col div{
    font-size:1.25rem;
}

.trade-recipient-record .filled .col div, .trade-your-record .filled .col div{
    color:#FBF6EF;
}

.finalize-trade{
    background:#FBF6EF;
    padding:20px;
    width:65%;
    min-height:250px;
}

.finalize-trade .btn{
    margin-top:20px;
}

.greyed{
    -webkit-filter:grayscale(100%);
            filter:grayscale(100%);
}

input[type=date]:not(.browser-default):focus:not([readonly])+label{
    color: #E23F35;
    font-family: 'KoHo', sans-serif;
}

/* label underline focus color */
input[type=date]:not(.browser-default):focus:not([readonly]){
    border-bottom: 1px solid #E23F35;
    box-shadow: 0 1px 0 0 #E23F35;
    font-family: 'KoHo', sans-serif;
}
  
/* icon prefix focus color */
.input-field .prefix.active {
    color: #E23F35;
    font-family: 'KoHo', sans-serif;
}

@media screen and (max-width:600px){
    .recipient-avatar, .recipient-rec-cover, .your-rec-cover{
        width:60%;
    }

    .make-trade-header{
        padding:0 20px;
    }

    .make-trade-body .col{
        margin:10px;
    }

    .trade-recipient .row{
        text-align:center;
    }
}

@media screen and (min-width:601px) and (max-width:992px){
    .recipient-avatar, .recipient-rec-cover{
        width:80%;
    }

    .your-rec-cover{
        width:65%;
    }
}

@media screen and (max-width:992px){
    .trade-recipient .row, .trade-recipient-record .row, .trade-your-record .row{
        text-align:center;
    }
}
.profile-header{
    text-align:center;
    padding:20px 20px 40px;
    height:300px;
    background:#E23F35;
    background-blend-mode:multiply;
    color:#FBF6EF;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-justify-content:center;
            justify-content:center;
}

.profile-header h1{
    margin:0;
    font-family: 'Kodchasan', sans-serif;
    font-weight:700;
    font-size:3.75rem;
}

.profile-header span{
    font-family: 'KoHo', sans-serif;
    font-size:1.5rem;
    padding:5px 5px 10px;
}

.profile-form{
    width:80%;
    margin:50px auto;
    padding:20px;
    background:#FBF6EF;

}

.avy-label{
  font-family: 'KoHo', sans-serif;
  color:#E23F35;
}

.form-btn{
    margin-bottom:20px;
    background:#1eabc4;
    font-family: 'KoHo', sans-serif;
    font-size:1.25rem;
    -webkit-transition:background 0.5s ease-out;
    transition:background 0.5s ease-out;
}

.form-btn:hover{
    background:#E23F35;
    -webkit-transition:background 0.2s ease-in;
    transition:background 0.2s ease-in;
}
body{
    background-size:cover;
    background-position:center;
    background-color:#F5E3CE;
    background-blend-mode:screen;
}

.add-record-header, .results-header{
    text-align:center;
}

.add-record-header h1, .results-header{
    font-family: 'Kodchasan', sans-serif;
}

.add-record-header span{
    font-family: 'KoHo', sans-serif;
    font-size:1.25rem;
    position:relative;
    bottom:10px;
    padding:5px 5px 10px;
}

.search-box{
    margin:20px 0;
}

.search-body{
    background:#E23F35;
    padding:20px;
}

.results-img{
    width:100%;
}

.row .col .results-details{
    background:#E23F35;
    min-height:41.5vh;
    padding:20px;
}

.check-record-btn,.add-record-btn{
    background:#1eabc4;
    font-family: 'KoHo', sans-serif;
    font-size:1.25rem;
    -webkit-transition:background 0.5s ease-out;
    transition:background 0.5s ease-out;
}

.check-record-btn:hover,.add-record-btn:hover{
    background:#E23F35;
    -webkit-transition:background 0.2s ease-in;
    transition:background 0.2s ease-in;
}

.search-box label{
    font-size:1.25rem;
    color:#F5E3CE;
}

.results-box label{
    font-size:1rem;
    color:#F5E3CE;
}

/* label focus color */
.search-box input[type=text]:not(.browser-default):focus:not([readonly])+label,input[type=password]:not(.browser-default):focus:not([readonly])+label,input[type=email]:not(.browser-default):focus:not([readonly])+label{
    color: #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

.results-box input[type=text]:not(.browser-default):focus:not([readonly])+label,input[type=password]:not(.browser-default):focus:not([readonly])+label,input[type=email]:not(.browser-default):focus:not([readonly])+label{
    color: #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

/* label underline focus color */
.search-box input[type=text]:not(.browser-default):focus:not([readonly]),input[type=password]:not(.browser-default):focus:not([readonly]),input[type=email]:not(.browser-default):focus:not([readonly]){
    border-bottom: 1px solid #F5E3CE;
    box-shadow: 0 1px 0 0 #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

.results-box input[type=text]:not(.browser-default):focus:not([readonly]),input[type=password]:not(.browser-default):focus:not([readonly]),input[type=email]:not(.browser-default):focus:not([readonly]){
    border-bottom: 1px solid #F5E3CE;
    box-shadow: 0 1px 0 0 #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

/* icon prefix focus color */
.search-box .input-field .prefix.active {
    color: #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

.results-box .input-field .prefix.active {
    color: #F5E3CE;
    font-family: 'KoHo', sans-serif;
}

@media screen and (max-width:600px){
    .add-record-btn{
        margin-top:20px;
    }
}
