.home-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color:#201F1B;
    overflow:hidden;
    font-family: 'KoHo', sans-serif;
    font-size: calc(10px + 1.25vmin);
}
  
.home-container a{
  color:#E23F35;
}

.landing-img-container{
  display:flex;
  align-items:center;
  justify-content:center;
}

.landing-img-container img{
  width:100vw;
  height:500px;
  object-fit:cover;
}

.landing-img-container h1{
  font-family: 'Kodchasan', sans-serif;
  position:absolute;
  color:#FBF6EF;
  padding-bottom:100px;
}

.landing-content{
  width:80%;
  margin:50px auto;
}

.box{
  background:#FBF6EF;
  margin-bottom:50px;
}

.row .content-header{
  background:#E23F35;
}

.content-header span{
  color:#FBF6EF;
  font-size:2.75rem;
  font-family: 'Kodchasan', sans-serif;
  font-weight:700;
}

.content-copy{
  font-size:1.375rem;
  padding:0 50px;
}

.profile-section{
  margin-top: 50px;
  width: 80%
}

.d-flex{
  display:flex;
}

.d-flex p{
  margin-top:10px;
  background-color:#E23F35;
  color:#FBF6EF;
  padding:20px;
}

.content-wrapper{
  display:flex;
  flex-direction:column;
  align-items:center;
}

.profilePic{
  border-radius:50%;
  width:300px;
}

.justify-content-center h1{
  font-family: 'Kodchasan', sans-serif;
}

.about-links a{
  transition:filter 0.5s ease-in;
}

.about-links a:hover{
  filter:drop-shadow(2px 2px 0 #E23F35);
  transition:filter 0.2s ease-in;
}

@media screen and (max-width:600px){
  .landing-img-container{
    top:112px;
  }

  .landing-img-container img{
    height:300px;
  }

  .landing-img-container h1{
    font-size:2.875rem;
    padding-bottom:75px;
  }

  .content-header span{
    font-size:1.5rem;
  }

  .content-copy{
    padding:10px 30px;
  }

  .justify-content-center h1{
    font-size:2.5rem;  
  }

  .d-flex p{
    padding:10px;
    font-size:2rem;
  }

  .profile-section{
    width:100%;
  }

  .profilePic{
    width:200px;
  }
}